<template>
	<div id="choose-gift">
		<search-header :hasBack="hasBack"></search-header>
		<div class="header">
			<router-link :to="{ name : 'ChooseActivity',params:{id:festivalAds.id} }" tag='div' class="festival">
				<img class="bg-img" :src="festivalAds.imagePath" onerror="this.onerror=null;this.src=''" alt="" />	
				<span class="text">节日推荐</span>
			</router-link>
			<router-link :to="{ name : 'ChooseActivity',params:{id:activityAds.id} }" tag='div' class="activity">
				<img class="bg-img" :src="activityAds.imagePath" onerror="this.onerror=null;this.src=''" alt=""/>	
				<span class="text">优惠活动</span>
			</router-link>
		</div>
		<div class="content">
			<div class="category-list" @click="spreadList(index)" v-for="(item,index) in categorys">
				<img class="bg-img" :src="item.imagePath" alt="" v-show="!spreadArr[index]"/>
				<div class="price" v-show="!spreadArr[index]">{{item.name}}</div>
				<!--展开的遮罩-->
				<div class="mask-wrap" v-show="spreadArr[index]">
					<img class="bg-img" :src="item.imagePath" alt=""/>
					<span class="category-text">{{item.name}}</span>
					<div class="list-wrap" >
						<ul class="clearfix" >
							<template v-for="(child,index) in item.categorys">
								<li :data-id='child.id' @click="toGoodsList(child.id)">{{child.name}}</li>
							</template>
							<!--<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>
							<li>价格区间</li>-->
						</ul>
					</div>
				</div>
			</div>
				
				
			<!--<div class="special">特殊日子</div>
			<div class="classifly">礼品类别</div>
			<div class="classifly">送礼对象</div>-->
		</div>
		<img class="cart-icon" src="../../assets/present_btn_shopping_cart@2x.png" @click="linkTo('Cart')"/>
	</div>
</template>

<script>
import SearchHeader from '@/components/Header/SearchHeader'
export default {
	data(){
		return{
			spread:false,
			spreadArr:[],
			hasBack:false,
			categorys:[],
			festivalAds:'',//节日推荐
			activityAds:'',//优惠活动
		}
	},
  components: {
  	SearchHeader
  },
	created(){
		this.getFestivalAds();
		this.getActivityAds();
//		this.getArticleList();
		this.getCategoryList();
	},
	methods:{
		spreadListInit(){
			let categorys = this.categorys
			for (var i = 0; i < categorys.length; i++) {
				this.spreadArr.push(false)
			}
		},
		toGoodsList(categoryId){
			let a = 3
			let type = 1
			this.$router.push({name:'GoodsList',query:{categoryId,a,type}})			
		},
		getFestivalAds(){
			this.$Api.getAdList(4).then((res)=>{
				console.log(res);
				if(res.q.s == 0){
					this.festivalAds = res.q.ads[0]
				}
			})
		},
		getActivityAds(){
			this.$Api.getAdList(5).then((res)=>{
				console.log(res);
				if(res.q.s == 0){
					this.activityAds = res.q.ads[0]
				}
			})
		},
		
		getCategoryList(){
			this.$Api.getCategoryList(1).then((res)=>{
				if(res.q.s==0){
					this.categorys = res.q.categorys;
					this.spreadListInit()
				}
			})
		},
		getArticleList(){
			this.$Api.getArticleList(1).then((res)=>{
				console.log(res)
			})
		},
		spreadList(index){
			this.spreadArr.splice(index,1,!this.spreadArr[index])
		},
	},

 
}
</script>



<style lang="scss">
	@import "../../sass/baseSass.scss";
	#choose-gift{
		height: 100%;
		background: #f0f0f0;
		.header{
			padding: 0.27rem 0.13rem;
			display: flex;
			>div{
				position: relative;
				font-size: 0.45rem;
				flex: 1;
				height: 2.67rem;
				line-height: 2.67rem;
				text-align: center;
				color: #fff;
			}
			.festival{
				margin-right: 0.13rem;
				/*background-image: url(../../assets/chooseGit/festival-pic.png);
				background-repeat: no-repeat;
				background-size: 100%;*/
			}
			.activity{
				/*background-image: url(../../assets/chooseGit/activity-pic.png);
				background-repeat: no-repeat;
				background-size: 100%;*/
			}
			.bg-img{
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
			}
			.text{
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}
		}
		.content{
			margin-bottom:1.6rem;

			>div{
				width: 100%;
				height: 4.0rem;
				line-height: 4.0rem;
				font-size: 0.45rem;
				margin-bottom: 0.27rem;
				text-align: center;
				color: #c59d76;
				background-repeat: no-repeat;
				background-size: contain;
			}
			.category-list{
				position: relative;
				.bg-img{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 4.0rem;
				}
				.mask-wrap{
					width: 100%;
					height: 100%;
					background-color: rgba(0, 0, 0, 0.4);
					color:#fff;
					font-size: 0.4rem;
					z-index: 1;
					.bg-img{
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0.4;
					}
					
				}
				.category-text{
					position: absolute;
					top: -35%;
					left: 50%;
					transform: translateX(-50%);
				}
				.list-wrap{
					/*width: 70%;*/
					width: 100%;
					height: 100%;
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					top: 1.47rem;
					>ul{
						li{
							float: left;
							width: 25%;
							height: 0.37rem;
							line-height: 0.37rem;
							margin-bottom: 0.4rem;
							@include textflow-one;
						}
					}
				}
			}
			.price{
				/*background-image: url(../../assets/chooseGit/price-pic.png);*/
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
			}
				
			.special{
				background-image: url(../../assets/chooseGit/special-pic.png);
			}
			.classifly{
				background-image: url(../../assets/chooseGit/classify-pic.png);
			}

		}
		.text{
			font-size: box(30);
		}
		.cart-icon{
			position: fixed;
			width: 1.2rem;
			height: 1.2rem;
			right: 0.4rem;
			bottom: 4.36rem;
			z-index: 100;
		}
	}
</style>


